import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

// Types
export interface AuditLog {
  id: number;
  user_id?: number;
  user_email?: string;
  user_name?: string;
  operation_type: 'CREATE' | 'UPDATE' | 'DELETE' | 'READ';
  display_operation?: string; // Enhanced operation display (e.g., "Login", "Logout")
  table_name: string;
  record_id?: string;
  old_values?: any;
  new_values?: any;
  changed_fields?: any[];
  ip_address?: string;
  user_agent?: string;
  browser_info?: string; // Extracted browser information
  request_method?: string;
  request_url?: string;
  request_body?: any;
  response_status?: number;
  execution_time_ms?: number;
  error_message?: string;
  metadata?: any;
  session_id?: string;
  transaction_id?: string;
  created_at: string;
}

export interface AuditLogFilters {
  user_id?: number;
  user_email?: string;
  operation_type?: 'CREATE' | 'UPDATE' | 'DELETE' | 'READ';
  table_name?: string;
  record_id?: string;
  start_date?: string;
  end_date?: string;
  limit?: number;
  offset?: number;
  sort_by?: string;
  sort_order?: 'ASC' | 'DESC';
}

export interface AuditLogResponse {
  success: boolean;
  message: string;
  data: AuditLog[];
  pagination: {
    total: number;
    limit: number;
    offset: number;
    hasMore: boolean;
    currentPage: number;
    totalPages: number;
  };
}

export interface AuditLogStats {
  operationStats: Array<{
    operation_type: string;
    count: number;
  }>;
  tableStats: Array<{
    table_name: string;
    count: number;
  }>;
  userStats: Array<{
    user_email: string;
    user_name: string;
    count: number;
  }>;
  dailyActivity: Array<{
    date: string;
    count: number;
  }>;
}

export interface ExportResult {
  success: boolean;
  message: string;
  data: {
    filename: string;
    filepath: string;
    recordCount: number;
    exportDate: string;
  };
}

// Get audit logs with filtering and pagination
export async function getAuditLogs(filters: AuditLogFilters = {}): Promise<AuditLogResponse> {
  try {
    const params = new URLSearchParams();
    
    Object.entries(filters).forEach(([key, value]) => {
      if (value !== undefined && value !== null && value !== '') {
        params.append(key, String(value));
      }
    });

    const res = await axiosInstance.get(`/api/audit-logs?${params.toString()}`);
    return res.data;
  } catch (error: any) {
    console.error("Error fetching audit logs:", error);
    const message = error.response?.data?.message || "Failed to fetch audit logs";
    toast.error(message);
    throw error;
  }
}

